"use client";

import { createContext, useContext, useEffect, useState } from "react";

const MyAuthContext = createContext();
export const AuthProvider = ({ children }) => {
    const [token,setToken]=useState(null);
    const [loggedIn,setloggedIn]=useState(false)
    const login=(newToken)=>{
    localStorage.setItem("token",`Bearer ${newToken}`);
    setToken(newToken);
    }
    const logout=()=>{
    localStorage.removeItem("token");
    setToken(null);
    }
    useEffect(()=>{
    const data=localStorage.getItem("token")
    if(data){
        setloggedIn(true);
    }
    },[])
  return (
    <>
      <MyAuthContext.Provider value={{login,logout,token,loggedIn}}>{children}</MyAuthContext.Provider>
    </>
  );
};
export const useAuth = () => useContext(MyAuthContext);
